* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
  background: #000;
}

.dot-grid-container {
  min-height: 100vh;
  width: 100%;
  background: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10vw;
  position: relative;
  padding: 2rem;
}

.dot-grid-wrapper {
  flex: 0 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.dot-grid {
  position: relative;
  width: 500px;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateZ(0);
  will-change: transform;
}

.dot {
  position: absolute;
  width: 3px;
  height: 3px;
  background-color: rgba(255, 248, 231, 0.75);
  border-radius: 50%;
  pointer-events: none;
  transform-origin: center;
  will-change: transform, opacity;
  filter: blur(0.5px);
  box-shadow: 0 0 3px rgba(255, 248, 231, 0.3);
}

/* Highlighted dots */
.dot[data-highlighted="true"] {
  background-color: rgba(255, 248, 231, 0.95);
  box-shadow: 0 0 4px rgba(255, 248, 231, 0.5);
}

/* For inner dots */
.dot[data-type="inner"] {
  width: 6px;
  height: 6px;
  background-color: rgba(255, 248, 231, 0.9);
}

/* When dot is active/animated */
.dot[data-active="true"] {
  width: 6px;
  height: 6px;
  box-shadow: 0 0 6px rgba(255, 248, 231, 0.5);
}

.text-content {
  flex: 0 1 auto;
  display: flex;
  flex-direction: column;
  gap: 4rem;
  max-width: 400px;
}

.main-title {
  font-family: 'EB Garamond', serif;
  color: rgba(255, 248, 231, 0.9);
  font-size: 32px;
  font-weight: 400;
  line-height: 1.2;
  margin: 0;
}

.text-section {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  align-items: flex-start;
}

.text-section h3 {
  font-family: 'EB Garamond', serif;
  color: rgba(255, 248, 231, 0.5);
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 0.5px;
  cursor: pointer;
  transition: color 0.3s ease;
  line-height: 1;
  margin: 0;
}

.text-section h3.active {
  color: rgba(255, 248, 231, 0.9);
}

.text-section h3:hover {
  color: rgba(255, 248, 231, 0.9);
}

@media (max-width: 1200px) {
  .dot-grid {
    width: 400px;
    height: 400px;
  }
  
  .dot {
    width: 2.5px;
    height: 2.5px;
  }
}

@media (max-width: 768px) {
  .dot-grid {
    width: 300px;
    height: 300px;
  }
  
  .dot {
    width: 2px;
    height: 2px;
  }
}

.scroll-button {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  background: transparent;
  border: 1px solid rgba(255, 248, 231, 0.9);
  color: rgba(255, 248, 231, 0.9);
  padding: 1rem 2rem;
  font-family: 'EB Garamond', serif;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.3s ease;
  opacity: 0;
  pointer-events: none;
}

.scroll-button.visible {
  opacity: 1;
  pointer-events: all;
}

.scroll-button:hover {
  background: rgba(255, 248, 231, 0.1);
}

/* Add responsive styles for the button */
@media (max-width: 768px) {
  .scroll-button {
    bottom: 1.5rem;
    padding: 0.8rem 1.6rem;
    font-size: 16px;
  }
}

.phase-button {
  background: none;
  border: none;
  color: rgba(255, 248, 231, 0.5);
  font-family: 'EB Garamond', serif;
  font-size: 18px;
  padding: 8px 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
  width: 100%;
  position: relative;
}

.phase-button:hover,
.phase-button.active {
  color: rgba(255, 248, 231, 0.9);
}

.phase-button:disabled {
  cursor: wait;
  opacity: 0.5;
}

.phase-button::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%) scaleX(0);
  width: 4px;
  height: 4px;
  background: rgba(255, 248, 231, 0.9);
  border-radius: 50%;
  transition: transform 0.3s ease;
}

.phase-button:hover::before,
.phase-button.active::before {
  transform: translateY(-50%) scaleX(1);
}